document.addEventListener('DOMContentLoaded', () => {
	const accordions = document.querySelectorAll('.aaccordion');

	accordions.forEach(el => {
		el.addEventListener('click', (e) => {
			const self = e.currentTarget;
			const selfcontrol = self.querySelector('.aaccordion__control');
			const selfcontent = self.querySelector('.aaccordion__content');


			self.classList.toggle('open');

			// если открыт аккордеон
			if (self.classList.contains('open')) {
				selfcontent.style.maxHeight = selfcontent.scrollHeight + 'px';
			} else {
				selfcontent.style.maxHeight = null;
			}

      accordions.forEach(ac => {
        if ((ac !== self) & (ac.classList.contains('open'))) {
          console.log(self)
          console.log(ac)
          const accontrol = ac.querySelector('.aaccordion__control');
			    const accontent = ac.querySelector('.aaccordion__content');
          ac.classList.toggle('open');
          accontent.style.maxHeight = null;

        }
      })

		});
	});
});
